import React, { Component } from "react";
import "./Calc.css";
import Display from "./components/Display";
import KeyPad from "./components/KeyPad";

class Calc extends Component {
    constructor(props) {
        super(props);
        this.state = {
            curExp: "",
            prevExp: "",
        };
        this.handleClick = this.handleClick.bind(this);
        this.operators = {
            "×": "*",
            "−": "-",
            "÷": "/"
        }
    }

    handleClick(keyValue) {
        // invalid checks
        const curExp = this.state.curExp;
        if (curExp === "" && /\D/.test(keyValue)) return;
        if (
            curExp &&
            /\D/.test(curExp[curExp.length - 1]) &&
            (!/^-?\d+\.?\d*$/.test(keyValue))
        ) return;

        switch(keyValue){
            // equals
            case "＝": {
                // replace spl chars
                this.setState(prevState => {
                    let result = null;
                    const exp = prevState.curExp.replace(/×|−|÷/, match => this.operators[match])
                    try { result = eval(exp) }
                    catch(err) {result = "Bad Expression"}

                    return {curExp:result+"", prevExp: prevState.curExp+""}
                })
                break;
            }
            // clear screen
            case "C": {
                console.log("test")
                this.setState({curExp:"", prevExp:""})
                break;
            }
            // backspace
            case "⌫": {
                this.setState(prevState => ({curExp: prevState.curExp.slice(0,-1)}))
                break;
            }
            // append
            default: {
                this.setState(prevState => ({curExp: prevState.curExp+keyValue+""}))
            }

        }
    }

    render() {
        return (
            <div className="calc">
                <Display
                    previous={this.state.prevExp}
                    current={this.state.curExp}
                />
                <KeyPad handleClick={this.handleClick} />
            </div>
        );
    }
}

export default Calc;
